.my_cft{
    position: relative;
   }



.my_cft:hover{
    cursor: pointer;
}

.my_cft div{
    position: absolute;
    bottom: 0;
    right: 0;
    background: #185a9d;
    color: white;
    margin-bottom: 5px;
    font-family: sans-serif;
    opacity: 0;
    visibility: hidden; 
    transition: visibility 0s, opacity 0.5s linear;
    border-bottom-left-radius: 20px;
    border-top-left-radius: 20px;
   }

.my_cft:hover div{
    width: 200px;
    padding: 8px 15px;
    visibility: visible;
    opacity: 1;
    }


    @media (max-device-width: 800px)
    {
        .root1{
            width:200px;
            margin:20px;
        }
    }
        
.progress {
    background-color:#d8d8d8;
    width:200px;
	margin: 15px 0;
    height:25px;
    border-radius: 20px;
    
}
.progress-done{
    border-radius: 20px;
    background-color: #185a9d;
    height: 100%;
	width: 0;
	opacity: 0;
    transition: 1s ease 0.3s;
    color:white;
    padding-top:12px;
}



